.tile-container {
    height: 100%;
    width: 100%;
    margin: 0.4% 1.3% 0.4% 1.3%;
}

.tile {
    border: 4px solid #280c06;
    border-radius: 30px;
    background: linear-gradient(140deg, #211c22, #646961), url(../../images/noise.png);
    background-blend-mode: normal;
    mix-blend-mode: hard-light;
    box-shadow: 0 20px 50px rgba(0, 0, 0, 0.3);
    opacity: 100%;
    height: 100%;
    width: 100%;
    transform-style: preserve-3d;
    color: #edeee0;
    line-height: 12vh;
    font-size: 4.5vmin;
    font-family: PressStart2P-Regular;
}

@media only screen and (max-width: 900px) {
    .tile {
        border: 3px;
        border-radius: 10px;
        line-height: 5.5vh;
    }
}

@font-face {
    font-family: 'PressStart2P-Regular';
    src: url('../../../fonts/PressStart2P-Regular.ttf');
}
